@charset "UTF-8";
.App {
  display: grid;
  grid-template-columns: [line1] 50px [line2] auto [line3] 200px [line4];
  grid-template-rows: [row-line1] 50px [row-line2] calc(100vh - 86px) [row-line3] 36px [row-line4];
  grid-template-areas: "sidebar-left main sidebar-right" "sidebar-left main sidebar-right" "sidebar-left main sidebar-right"; }
  @media (max-width: 768px) {
    .App {
      grid-template-rows: [row-line1] 50px [row-line2] calc(100vh - 86px) [row-line3] 36px [row-line4]; } }
  .App .App-main-area {
    /*as a child*/
    grid-area: main;
    background: #5d5b61;
    padding: 15px;
    overflow-y: auto;
    /*as a parent*/
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(180px, 1fr));
    grid-auto-rows: 300px;
    grid-gap: 15px; }
    @media (max-width: 768px) {
      .App .App-main-area {
        grid-area: row-line2 / line1 / row-line3 / line4;
        grid-template-columns: repeat(auto-fill, minmax(calc(50vw - 6vh), 1fr)); } }

