.CheckboxBar {
  background: #270486;
  grid-area: sidebar-left;
  padding: 5px;
  display: flex;
  flex-direction: column; }
  @media (max-width: 768px) {
    .CheckboxBar {
      grid-area: row-line1 / line1 / row-line2 / line4;
      flex-direction: row; } }
  .CheckboxBar .CheckboxBar-checkbox input {
    display: none; }
    .CheckboxBar .CheckboxBar-checkbox input:checked ~ .CheckboxBar-label {
      background: #4339c7; }
  .CheckboxBar .CheckboxBar-checkbox .CheckboxBar-label {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 40px;
    width: 40px;
    background: #14103c;
    margin-bottom: 5px;
    margin-right: 0px;
    cursor: pointer;
    color: #fff; }
    @media (max-width: 768px) {
      .CheckboxBar .CheckboxBar-checkbox .CheckboxBar-label {
        margin-bottom: 0px;
        margin-right: 5px; } }
