.ResultBar {
  grid-area: sidebar-right;
  background: #343338;
  padding: 5px;
  overflow-x: auto;
  box-shadow: 0px -1px 5px 0px rgba(0, 0, 0, 0.3);

  @media(max-width: 768px) {
    grid-area: row-line3 / line1 / row-line4 / line4;
  }

  .ResultBar-inner {
    @media(max-width: 768px) {
      position: fixed;
      height: 36px;
      bottom:0px;
      overflow-y: auto;
      width: 100%;
      right: 0px;
      left: 0px;
    }

    .ResultBar-result {
      display: flex;
      align-content: center;
      justify-content: space-between;
      background: #ececec;
      border-bottom: 1px solid #b3afb8;
      cursor: pointer;

      &:first-child {
        border-top-left-radius: 5px;
        border-top-right-radius: 5px;
        overflow: hidden;
      }

      &:last-child {
        border-bottom-left-radius: 5px;
        border-bottom-right-radius: 5px;
        overflow: hidden;
        box-shadow: 0 10px 6px -6px #19191b;
      }

      .ResultBar-content {
        padding: 5px 5px 5px 10px;
        font-size: 21px;
        flex: 1;
      }

      .ResultBar-button {
        display: flex;
        align-items: center;
        justify-content: center;
        border: none;
        color: #e4e1ea;
        padding: 0px;
        width: 40px;
        outline: none;
        cursor: pointer;
        background: transparent;

        &:before {
          content: "\0000D7";
          display: block;
          margin-bottom: 3px;
          font-size: 28px;
          transition: color 0.3s;
        }

        &:hover:before {
          color: #ff007d;
        }
      }
    }
  }
}

.example-enter {
  // margin-left: 100%;
  transform: translateX(calc(100% + 15px));
}

.example-enter.example-enter-active {
  // margin-left: 0%;
  transform: translateX(0px);
  transition: transform 200ms ease-in;
}

.example-leave {
  // margin-left: 0%;
  transform: translateX(0px);
}

.example-leave.example-leave-active {
  // margin-left: 100%;
  transform: translateX(calc(100% + 15px));
  transition: transform 200ms ease-in;
}