.Dice {
	display: flex;
	flex-direction: column;
	align-items: stretch;
	justify-content: space-between;
	background: #fff;
	cursor: pointer;
	outline: none;
	-webkit-box-shadow: 0 10px 6px -6px #232323;
	-moz-box-shadow: 0 10px 6px -6px #232323;
	box-shadow: 0 10px 6px -6px #232323;

	overflow: hidden;
	border-radius: 5px;
	background: #423bc8;
	// #4c6bdc
	// #6e86dc

	&:focus {
		.Dice-result {
			color: #333;
		}
	}

	.Dice-wrapper {
		flex: 1;
		display: flex;
		align-items: center;
		justify-content: center;
		position: relative;

		.Dice-dice {
			height: 140px;
			width: 140px;
		}

		.Dice-image {
			max-width: 141px;
			position: absolute;
			transform: rotate(0deg);

			&.rotate {
				transform: rotate(360deg);
				transition: transform 0.3s ease-in-out;
			}
		}
	}

	.Dice-result {
		display: flex;
		align-items: center;
		justify-content: center;
		// background: #8df5df;
		background: #ececec;
		height: 70px;
		font-size: 3em;
		color: rgba(51, 51, 51, 0.50);
	}
}
